
import { defineComponent } from 'vue';
import Job from './Job.vue';
import { Job as JobModel } from '../models/job';
import Filters from './Filters.vue';
import jobsService from '../services/job';

type ComponentData = {
  jobs: JobModel[];
  activeFilters: string[];
};

export default defineComponent({
  name: 'Home',
  components: { Job, Filters },
  data(): ComponentData {
    return {
      jobs: [],
      activeFilters: []
    };
  },
  mounted() {
    this.loadJobs();
  },
  methods: {
    onTagClick(value: string): void {
      const filterAlreadyExists = this.activeFilters.some(f => f === value);

      if (!filterAlreadyExists) {
        this.activeFilters = [value, ...this.activeFilters];
        this.loadJobs();
      }
    },

    onFiltersClear(): void {
      this.activeFilters = [];
      this.loadJobs();
    },

    onFilterRemove(filter: string): void {
      this.activeFilters = this.activeFilters.filter(f => f !== filter);
      this.loadJobs();
    },

    loadJobs(): void {
      jobsService.getJobs(this.activeFilters).then(jobs => {
        this.jobs = jobs;
      });
    }
  }
});
