
import { defineComponent, PropType } from 'vue';
export default defineComponent({
  name: 'Filters',
  props: {
    activeFilters: {
      type: Array as PropType<string[]>,
      required: true
    }
  },
  methods: {
    onClear(): void {
      this.$emit('clear');
    },

    onRemove(filter: string): void {
      this.$emit('remove', filter);
    }
  }
});
